<template>
  <div class="common-info">
    <div class="topTitle">
      <span class="topTitle-span1">客户视图</span>
      <span class="topTitle-span1">
        <img src="@/assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">证券账户信息</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>{{item.code}}</td>
                    <td>{{item.code}}</td>
                    <td>{{item.code}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.code}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.code}}</td>
                    <td>{{item.name}}</td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
        </div>
        <div class="flexCenter pageBox">
          <Page :total="100" show-elevator show-sizer></Page>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      showModal: false,
      dataObj: {
        type: "1", //1新增，2修改，3删除，4历史，5设置
        data: []
      },
      modelTitle: "新增",
      type: this.$route.params.type,
      tableHeader: [
        { name: "序" },
        { name: "账户类别" },
        { name: "证券账号" },
        { name: "账户状态" },
        { name: "开户方式" },
        { name: "开户日期" },
        { name: "销户日期" },
        { name: "业务部门" },
        { name: "来源系统编码" }
      ],
      tableBody: [
        {
          id: "12",
          code: "111",
          name: "低",
          roleDescribe: "0",
          Remarks: "50"
        }
      ],
    };
  },
  mounted() {},
  methods: {
    subTaskManage() {
      this.$router.push({ name: "subTaskManage" });
    }
  },
  watch: {}
};
</script>
<style lang="scss">
</style>


